.phase-test {
  position: relative;
  width: 50%;
  margin: 0px 0px 0px 40px;
  border-radius: 5px;
  overflow: hidden;
  background-image: url("../img/repairing-min.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__wrap {
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 40px 33px 40px 33px;
    border-radius: 5px;
    width: 65%;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: flex-start;
    flex-direction: column;
    background-color: var(--light-color);
    transform: translate(-50%, -50%);
    overflow: hidden;
    z-index: 10;
  }

  &__checklist {
    display: flex;
    margin: 20px 0px 40px 0px;
    align-self: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
  }

  &__check {
    font-size: 15px;
    font-weight: 400;
    line-height: 23px;
    color: var(--text-grey);
    position: relative;
    margin-left: 20px;
    padding: 0px 0px 7px 0px;
    width: 40%;
    white-space: nowrap;
    flex-wrap: nowrap;
    white-space: wrap;
  }

  &__button:hover .move-right {
    transition: all 0.2s ease;
    animation-name: move-reverse;
    animation-direction: alternate;
    animation-duration: 0.4s;
    animation-iteration-count: infinite;
  }
}