.our-location {
  padding-top: var(--margin-main);
  padding-bottom: var(--margin-main);
  background-color: var(--light-color);

  &__container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;

  }

  &__contacts {
    width: 50%;
    color: var(--text-color);
    position: relative;
    z-index: 5;
  }

  &__text {
    padding: 0 20px;
  }

  &__title {
    padding: 0 20px;
  }

  &__form {
    width: 100%;
  }
}

.map {
  width: 50%;
  height: 500px;
  background-color: grey;
}

[class*='copyrights-pane'] {
  display: none !important;
}

.bg {
  background-image: url("../../img/imagelamps.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  rotate: 180deg;
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: .12;
  width: 100%;
  height: 100%;
}

.information {
  padding: 0 20px;

  .tel-icon {
    position: absolute;
    top: 2px;
    left: 0px;
    color: var(--text-color);
  }

  .email-icon {
    position: absolute;
    top: 2px;
    left: 0px;
    color: var(--text-color);
  }

  &__telefone-link {
    display: inline;
    color: #000;
  }

  &__telefone,
  &__fax,
  &__email,
  &__address,
  &__hourse-open {
    padding-bottom: 10px;
    font-weight: 500;
    position: relative;
  }

  &__link {
    color: var(--text-color);
    font-weight: 500;
  }

  &__telefone,
  &__email {
    padding-left: 24px;
  }


  &__email {
    padding-bottom: 40px;
    margin-bottom: 40px;
    width: 45%;
    border-bottom: 1px solid #000;
  }
}