.testing-product {
  position: relative;
  background-image: url("../img/vecteezy_close-up-photo.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 600px;


  &__info {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    padding: 40px 0px 0px 20px;
    border-radius: 4px;
    margin: 0px auto;
    background-color: var(--light-color);
    opacity: 0.9;
    box-shadow: -6px 4px 11px 2px rgba(34, 60, 80, 0.48);
  }

  &__table {
    width: 30%;
    margin-bottom: 30px;


    &>.phase-test__checklist {
      margin: 0;
      margin-bottom: 30px;
    }

    &>.phase-test__button {
      margin-bottom: 30px;
    }
  }

  &__title,
  &__text {
    width: 80%;
    color: var(--text-color);
    padding: 0;
    margin: 0;
    padding-bottom: 20px;
  }

  &__title {
    font-size: 30px;
  }

  &__text {
    font-size: 14px;
  }
}