.header {
  padding: 23px 0;

  &__container {
    @include flex-h-center;
    justify-content: space-between;
    align-items: flex-end;
    // flex-wrap: wrap;
  }

  &__logo {
    min-width: 195px;
    // width: 195px;
    height: 64px;
    display: block;
    background-image: url('../img/contrast_logo_transperent.png');
    background-repeat: no-repeat;
    background-size: contain;

  }

  &__nav {
    width: 52%;
  }

  &__contacts-list {
    // margin-left: 200px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-self: flex-start;
    flex-wrap: wrap;
    width: 16%;
  }

  &__contact {
    position: relative;
    margin-left: 14px;
    padding-left: 0px;
    color: var(--color-metall);
    line-height: 23px;
    font-size: 13px;
    width: 85%;

    &:not(:last-child) {
      margin-bottom: 3px;
      width: 76%;
    }

    &:nth-child(2) {
      margin-left: 0px;
    }

  }

  &__link {
    position: relative;
    color: var(--color-metall);
    transition: 0.5s color ease-in-out;

    &:hover {
      color: var(--main-color);
      transition: 0.5s color ease-in-out;
    }

    &:not(:last-child) {
      margin-bottom: 3px;
    }
  }
}

.nav {

  &__sub-list {
    display: none;
    position: absolute;
    top: 50px;
    left: 0;
    min-width: 134px;
    background-color: var(--background-color);
    border-radius: 5%;
  }

  &__sub-item {
    display: block;
    padding: 13px 7px;
    position: relative;
    transition: all 0.4s ease-in-out;

    .nav__link::before {
      position: absolute;
      width: 0%;
      height: 2px;
      bottom: 0px;
      left: 0;
      content: "";
    }

    &:hover {
      display: block;
      transition: all 0.4s ease-in-out;

      .nav__link::before {

        position: absolute;
        width: 100%;
        height: 2px;
        bottom: 0px;
        left: 0;
        content: "";
        transition: width 0.4s ease-in-out;
        background-color: var(--main-color);

      }

    }

    &_active {
      transition: all 0.4s ease-in-out;

      &::after {
        position: absolute;
        width: 113%;
        height: 2px;
        bottom: 0px;
        left: -5px;
        content: "";
        transition: all 0.4s ease-in-out;
        background-color: var(--main-color);
      }
    }
  }

  &__item {
    display: inline;
    padding: 13px 7px;
    position: relative;
    transition: background-color 0.4s;



    &::before {
      position: absolute;
      width: 100%;
      height: 2px;
      bottom: 0px;
      left: 0;
      content: "";
      opacity: 0;
      transition: opacity 0.4s;
      background-color: var(--main-color);
    }

    .fa-lg {
      transform: rotate(0deg);
      transition: transform 0.4s;
    }

    &:hover {
      transition: opacity 0.4s;

      & .nav__sub-list {
        display: block;
      }

      &::before {
        position: absolute;
        width: 100%;
        height: 2px;
        bottom: 0px;
        left: 0;
        content: "";
        transition: opacity 0.4s;
        opacity: 1;
        background-color: var(--main-color);
      }

      .fa-lg {
        color: var(--main-color);
        transform: rotate(180deg);
        transition: transform 0.4s;
      }
    }

    &_active {
      transition: background-color 0.9s;

      &::after {
        position: absolute;
        width: 113%;
        height: 2px;
        bottom: 0px;
        left: -5px;
        content: "";
        transition: background-color 0.9s;
        background-color: var(--main-color);
      }
    }
  }

  &__link {
    color: var(--light-color);
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 2px;
  }
}

.tel-icon-header {

  position: absolute;
  top: 4px;
  left: -17px;
  color: var(--color-metall);

}

.email-icon-header {

  position: absolute;
  top: 4px;
  left: -17px;
  color: var(--color-metall);

}

.basket-btn {
  position: relative;

  & .fa-basket-shopping {
    font-size: 22px;
    color: var(--light-color);
  }

  & .amount-product {
    font-size: 13px;
    top: -8px;
    right: -16px;
    width: 20px;
    height: 20px;
    text-align: center;
    position: absolute;
    line-height: 22px;
    border-radius: 50%;
    display: block;
    background-color: var(--main-color);

  }
}

.custom.animate-open {
  opacity: 1;

  transition: transform var(--transition-time), opacity var(--transition-time);
}