.breadcrumbs {
  @include flex-v-center;

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 22px;


      &:hover .breadcrumbs__link {

        color: var(--main-color);
        transition: 0.5s color ease-in-out;

      }


      &::after {
        content: "/";
        position: absolute;
        right: -14px;
        top: 1px;
        color: #9a9ca5;
      }
    }

    &:last-child {
      & .breadcrumbs__link {
        font-size: 14px;
        opacity: 0.6;
      }
    }
  }

  &__link {
    font-size: 15px;
    line-height: 20px;
    color: var(--color-gray);
    transition: 0.5s color ease-in-out;

    &:not([href]) {
      color: #9a9ca5;
    }
  }
}