.video-block {
  width: 100%;
  position: relative;
  overflow: hidden;
  border-radius: 5px;
  height: 565px;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1;
    background-color: rgba(#000, 0.6);
    transition: opacity 0.4s ease-in-out, visibility 0.4s ease-in-out;
  }

  &--played {
    &::before {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__content {
    display: block;
    width: var(--content-width);
    height: 100%;
    object-fit: cover;
  }

  &__play {
    @include flex-all-center;
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 20;
    transform: translate(-50%, -50%);
    transition: opacity 0.4s ease-in-out, visibility 0.4s ease-in-out;

    svg {
      fill: var(--light-color);
      width: 100px;
      height: 100px;
    }

    &--played {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__muted {
    @include flex-all-center;
    position: absolute;
    left: 52px;
    bottom: 22px;
    z-index: 20;
    border-radius: 100%;
    width: 40px;
    height: 40px;
    background-color: rgba(#fff, 0.12);
    transform: translate(-50%, -50%);

    svg {
      fill: var(--light-color);
      width: 40x;
      height: 40px;
    }
  }
}