.partners {
  padding-bottom: var(--margin-main);
  background-color: var(--light-color);

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    display: grid;
    column-gap: 102px;
    grid-template-columns: repeat(4, 1fr);
    row-gap: 40px;

    @include minidesk {
      column-gap: 30px;
    }
  }

  &__item {
    align-self: end;

    // @include minidesk {
  //   align-self: end;
  //   display: flex;
  //   justify-content: center;
  //   grid-column: 3 span;
  // }
  }

  &__link {
    &:focus {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }
  }

  &--about {
    padding: 120px 0;
    background-color: var(--color-light);

    .partners__title {
      margin-bottom: 25px;
    }
  }
}
