.video-on-start {
  position: absolute;
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
  overflow: hidden;
  opacity: 1;

  &__content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;

  }
}

.remove {
  animation: ani-video 2s forwards;

}

@keyframes ani-video {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}