.new-product {
  padding-bottom: 110px;
  background-color: var(--light-color);

  &__container {
    display: flex;
    justify-content: center;

  }

  &__products {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(1fr, 1fr);
    width: 50%;
  }

  &__title {
    margin-bottom: 20px;
  }
}

.check-mark {
  &::before {
    position: absolute;
    top: 4px;
    left: -22px;
    // font: var(--fa-font-solid);
    // content: "\f00c";
    color: var(--main-color);
  }
}