.product {
  position: relative;
  @include flex-v-center;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0;
  margin: 0;
  border: 1px solid;
  border-style: solid;
  border-width: 1px 1px 1px 1px;
  border-color: var(--color-metall);
  border-radius: 5px 5px 5px 5px;
  font-size: 15px;
  font-weight: 400;
  background-color: var(--light-color);
  overflow: hidden;

  &:hover>.featured-product__box.image-box {

    transition: bottom 0.5s;
    bottom: 110px;
  }

  &__flag {
    position: absolute;
    padding: 5px 10px;
    font-size: 12px;
    font-weight: 500;
    position: absolute;
    text-align: center;
    line-height: 1.2em;
    top: 30px;
    left: 0;
    margin: 0;
    background-color: var(--text-color);
    color: #fff;
    z-index: 9;
  }

  &__rating {
    position: relative;
    margin: 0 auto 5px;
    display: block;
    float: none;
    height: 1em;
  }

  &__title {
    font-size: 14px;
    font-weight: 500;
    text-align: center;
    line-height: 17px;
    color: var(--text-color);
  }

  &__price {
    color: var(--text-color);
    font-size: 18px;
    line-height: 21px;
    font-weight: 600;
    text-align: center;
  }

  &__details {
    background-color: var(--light-color);
    margin-top: 40px;
    z-index: 10;
  }

  &__old-price {
    color: var(--main-color);
  }

  &__img {
    object-fit: cover;
    // width: 80%;
    height: 175px;
  }

  &__star-text {
    font-size: 0px;
  }
}

.raitng {

  &__list {
    text-align: center;

  }

  &__item {
    display: inline-block;
    font-size: 15px;
    font-weight: 900;
    color: var(--text-grey);
    transition: color 0.4s;
    cursor: pointer;

    &:hover {
      transition: color 0.4s;
      color: var(--main-color);
    }
  }
}
