.product-gallery {
  background-color: var(--light-color);
  padding-top: var(--margin-main);

  &__container {
    max-height: 660px;

  }

  &__swiper {
    height: 100%;

    &>.swiper-slide {
      flex-wrap: wrap !important;
    }
  }

  & .swiper-slide {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    & .product {
      width: 220px;
    }
  }

  & .swiper-pagination {
    margin: 20px 0px;
    bottom: 0px;
    position: relative;
    text-align: center;
    z-index: 10;
  }

  & .swiper-pagination-bullet {
    width: 40px;
    height: 40px;
    border-radius: 15%;
    text-align: center;
    line-height: 40px;
    font-size: 16px;
    color: #000;
    background: #fff;
    opacity: 1;
  }


  & .swiper-pagination-bullet-active {
    color: #fff;
    background: var(--text-color);
  }
}