.form-message {
  background-color: var(--light-color);
  padding-bottom: var(--margin-main);

  &__container {
    border-radius: 5px;
    background-color: var(--modal-color);

  }
}

.form {
  &__form-contacts {
    display: flex;
    flex-direction: column;

    padding: 10px;
  }

  &__form-send {
    align-self: center;
    width: 30%;
    border-style: none;
  }
}

.form-field {
  &:not(:last-child) {
    margin-bottom: 16px;
  }

  &__caption {
    font-size: 14px;
    line-height: 22px;
    color: var(--text-grey);
  }

  &__input {
    border-style: none;
    border-radius: 4px;
    font-size: 18px;
    line-height: 26px;
    width: 100%;
    color: var(--text-color);
  }

}