.featured-product {
  background-color: var(--light-color);
  // margin-top: 80px;
  padding-bottom: 60px;

  &__title {
    margin-bottom: 20px;
  }

  &__container {
    overflow: hidden;
  }

  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__item {

    &::after {
      position: absolute;

    }

    &:hover {
      cursor: pointer;
      transition: background-color 0.4s;
      background-color: var(--halfmain-color);
    }

    .product__img {
      padding: 40px 0 0 0;
      // width: 54%;
    }
  }

  &__box {
    position: relative;
  }
}

.swiper.swiper-featured {
  margin: 0 10px 0 -10px;
  padding: 0 0 40px 0;
  overflow: visible;

  .swiper-pagination-bullet {
    border-radius: 2px;
    width: 8px;
    height: 3px;
    transition: width 0.3s;

    .swiper-pagination-bullet-active {
      transition: width 0.3s;
      width: 20px;
      background-color: var(--main-color);
    }
  }
}
